<template>

    <el-container style="border: 1px solid #eee" >
      <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
       <div width="200px">
         <el-autocomplete
             style="width: 200px"
             class="inline-input"
             v-model="state2"
             :fetch-suggestions="querySearch"
             placeholder="请输入内容"
             :trigger-on-focus="false"
             @select="handleSelect"
           ></el-autocomplete>
       </div>
       <div><el-button type="primary" style="width: 200px;margin-top: 10px;" @click="findByName"  >查询</el-button></div>
      </el-aside>
      <el-container direction="vertical">
        <!-- 表格 -->
          <el-main>
              <el-table :data="tableData.slice((currentPage-1)*pageSize,currentPage*pageSize)"  >
                <el-table-column type="index" label="序号" width="140" ></el-table-column>
                <el-table-column prop="id" label="学校标识码" width="120"></el-table-column>
                <el-table-column prop="name" label="学校名称"></el-table-column>
                <el-table-column prop="district" label="所在地区"></el-table-column>
                <el-table-column prop="city" label="所在城市"></el-table-column>
                <el-table-column prop="department" label="主管部门"></el-table-column>
                <el-table-column prop="level" label="办学层次"></el-table-column>
                <el-table-column prop="type" label="学校类型"></el-table-column>
              </el-table>
          </el-main>
          <!-- 分页器 -->
          <div class="block" style="margin-top:15px;">
              <el-pagination align='center' @size-change="handleSizeChange" @current-change="handleCurrentChange"
              :current-page="currentPage"
              :page-sizes="[15,20,35,40]"
              :page-size="pageSize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="tableData.length">
              </el-pagination>
          </div>

      </el-container>


    </el-container>

</template>

<style>

</style>

<script>
export default {
  data() {
    return {
      tableData:[],
      tableDataCopy:[],
      restaurants: [],
      open_list:["1"],
      state2: '',
      currentPage: 1, // 当前页码
      total: 2744, // 总条数
      pageSize: 15 // 每页的数据条数
    }
  },
  methods: {
    //每页条数改变时触发 选择一页显示多少行
    handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
        this.currentPage = 1;
        this.pageSize = val;
    },
    //当前页改变时触发 跳转其他页
    handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
        this.currentPage = val;
    },
    findByName(){
      // alert(this.state2)
      // alert(12)
      // console.log(this.state2)
      this.currentPage = 1
      this.tableData = this.tableDataCopy.filter(this.createFilter2(this.state2))
    },
    querySearch(queryString, cb) {
      var restaurants = this.restaurants;
      var results =  restaurants.filter(this.createFilter(queryString));
      // 调用 callback 返回建议列表的数据
      // console.log(results)
      cb(results);
    },
    createFilter(queryString) {
      return (restaurant) => {
        return (restaurant.value.indexOf(queryString) != -1);
      };
    },
    createFilter2(queryString) {
      return (restaurant) => {
        return (restaurant.name.indexOf(queryString) != -1);
      };
    },
    getData(){
        var _this = this;   //很重要！！
        this.$axios({url:"http://localhost:8080/findAll"})
        .then( function (res) {
           // console.log(res);
           _this.tableData = res.data
           _this.tableDataCopy = res.data
           _this.restaurants = res.data.map(v => { return { value: v.name}})
           // console.log(_this.restaurants);
         })
        .catch(function (error) {console.log(error);});
    },
    handleSelect(item) {
      // alert(1)
      console.log(item);
    }
  },
  mounted:function () {
    this.getData()
  },
}
</script>
