<template>
    <el-container style="height: 600px; border: 1px solid #eee" >
      <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
          <el-menu :default-openeds="open_list"  :unique-opened="isUnique" >
              <el-submenu index="1">
                <template slot="title"  ><i class="el-icon-plus"></i>高校类型</template>
                    <el-menu-item index="1-1" @click="byType('一流大学',$event)"  >一流大学</el-menu-item>
                    <el-menu-item index="1-2" @click="byType('一流学科',$event)"  >一流学科</el-menu-item>
                    <el-menu-item index="1-3" @click="byType('985',$event)" >985高校</el-menu-item>
                    <el-menu-item index="1-4" @click="byType('211',$event)" >211高校</el-menu-item>
              </el-submenu>
              <el-submenu index="2" id="el2"  >
                <template slot="title"><i class="el-icon-plus"></i>所在地区</template>
                    <el-menu-item index="2-1" @click="byDistrict" >北京市</el-menu-item>
                    <el-menu-item index="2-2" @click="byDistrict" >四川省</el-menu-item>
                    <el-menu-item index="2-3" @click="byDistrict" >天津市</el-menu-item>
                    <el-menu-item index="2-4" @click="byDistrict" >河北省</el-menu-item>
                    <el-menu-item index="2-5" @click="byDistrict" >山西省</el-menu-item>
                    <el-menu-item index="2-6" @click="byDistrict" >内蒙古自治区</el-menu-item>
                    <el-menu-item index="2-7" @click="byDistrict" >辽宁省</el-menu-item>
                    <el-menu-item index="2-8" @click="byDistrict" >吉林省</el-menu-item>
                    <el-menu-item index="2-9" @click="byDistrict" >黑龙江省</el-menu-item>
                    <el-menu-item index="2-10" @click="byDistrict" >上海市</el-menu-item>
                    <el-menu-item index="2-11" @click="byDistrict" >江苏省</el-menu-item>
                    <el-menu-item index="2-12" @click="byDistrict" >浙江省</el-menu-item>
                    <el-menu-item index="2-13" @click="byDistrict" >安徽省</el-menu-item>
                    <el-menu-item index="2-14" @click="byDistrict" >福建省</el-menu-item>
                    <el-menu-item index="2-15" @click="byDistrict" >江西省</el-menu-item>
                    <el-menu-item index="2-16" @click="byDistrict" >山东省</el-menu-item>
                    <el-menu-item index="2-17" @click="byDistrict" >河南省</el-menu-item>
                    <el-menu-item index="2-18" @click="byDistrict" >湖北省</el-menu-item>
                    <el-menu-item index="2-19" @click="byDistrict" >湖南省</el-menu-item>
                    <el-menu-item index="2-20" @click="byDistrict" >广东省</el-menu-item>
                    <el-menu-item index="2-21" @click="byDistrict" >广西壮族自治区</el-menu-item>
                    <el-menu-item index="2-22" @click="byDistrict" >海南省</el-menu-item>
                    <el-menu-item index="2-23" @click="byDistrict" >重庆市</el-menu-item>
                    <el-menu-item index="2-24" @click="byDistrict" >贵州省</el-menu-item>
                    <el-menu-item index="2-25" @click="byDistrict" >云南省</el-menu-item>
                    <el-menu-item index="2-26" @click="byDistrict" >西藏自治区</el-menu-item>
                    <el-menu-item index="2-27" @click="byDistrict" >陕西省</el-menu-item>
                    <el-menu-item index="2-28" @click="byDistrict" >甘肃省</el-menu-item>
                    <el-menu-item index="2-30" @click="byDistrict" >青海省</el-menu-item>
                    <el-menu-item index="2-31" @click="byDistrict" >宁夏回族自治区</el-menu-item>
                    <el-menu-item index="2-32" @click="byDistrict" >新疆维吾尔自治区</el-menu-item>
            </el-submenu>
          </el-menu>
      </el-aside>
      <el-container direction="vertical">
         <div class="ti"><span>{{show}}</span></div>
          <el-main>
              <el-table :data="tableData.slice((currentPage-1)*pageSize,currentPage*pageSize)"  >
                <el-table-column type="index" label="序号" width="140" ></el-table-column>
                <el-table-column prop="id" label="学校标识码" width="120"></el-table-column>
                <el-table-column prop="name" label="学校名称"></el-table-column>
                <el-table-column prop="district" label="所在地区"></el-table-column>
                <el-table-column prop="city" label="所在城市"></el-table-column>
                <el-table-column prop="department" label="主管部门"></el-table-column>
                <el-table-column prop="level" label="办学层次"></el-table-column>
                <el-table-column prop="type" label="学校类型"></el-table-column>
              </el-table>

          </el-main>
          <!-- 分页器 -->
          <div class="block" style="margin-top:15px;">
              <el-pagination align='center' @size-change="handleSizeChange" @current-change="handleCurrentChange"
              :current-page="currentPage"
              :page-sizes="[15,25,30,40]"
              :page-size="pageSize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="tableData.length">
              </el-pagination>
          </div>

      </el-container>
    </el-container>

</template>
<style type="text/css">

  .el-aside {
    color: #333;
  }
.t{
    margin: 0 4px;
}
.ti{
    text-align: center;
    font-family: Helvetica;
}

</style>

<script>

import HelloWorld from '@/components/HelloWorld.vue'

export default {
  name: 'Home',
  components: {
    HelloWorld
  },
  data() {
    return {
      tableData:[],
      open_list:["1"],
      show:"一流大学",
      isUnique:true,
      currentPage: 1, // 当前页码
      total: 2744, // 总条数
      pageSize: 15 // 每页的数据条数
    }
  },
  mounted:function () {
              var _this = this   //很重要！！
              this.$axios({
                url: "http://localhost:8080/findByType",
                params: {type: "一流大学"}}).then(function (res) {
                                        console.log(res);
                                       _this.tableData = res.data}).catch(function (error) {console.log(error);});
          },
  methods:{
    //每页条数改变时触发 选择一页显示多少行
    handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
        this.currentPage = 1;
        this.pageSize = val;
    },
    //当前页改变时触发 跳转其他页
    handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
        this.currentPage = val;
    },
    byType:function(msg,event){
      // console.log(event)
      // 发送请求, 默认为get请求
      this.$axios({
        url: "http://localhost:8080/findByType", params: {type: msg}
      }).then(res => {
        this.currentPage = 1;

        // console.log(res);
        this.tableData = res.data
        this.show = event.$el.innerText

      })
    },
    byDistrict:function(event){
      // alert(event.$el.innerText)
      // 发送请求, 默认为get请求
      this.$axios({
        url: "http://localhost:8080/findByDistrict",params: {district: event.$el.innerText}
      }).then(res => {
        // console.log(res.data);
        this.currentPage = 1;
        this.tableData = res.data
        this.isCollapse = true
        this.show = event.$el.innerText
      })
    }
  }
}
</script>
